@import "assets/styles/variables";

.app-wrapper {
  display: grid;

  width: 1200px;
  height: 100vh;
  margin: 0 auto;
  grid-template-areas:
  'h h'
  'n c';
  grid-template-rows: 60px 1fr;
  grid-template-columns: 2fr 10fr;

  .app-wrapper-content {
    grid-area: c;
    background-color: white;
  }
}

